<template><section><html><head></head><body><h1>圆形扫描</h1>
<p><code v-pre="">vc-scan-circle</code> 组件用于加载圆形扫描效果，实质是通过 <code v-pre="">vc-stage-process-post</code> 加载的一个后期处理特效。参考 <a href="https://www.wellyyss.cn/ysCesium/main/app.html">ysCesium|跃焱邵隼</a> 。</p>
<h2>示例</h2>
<h3>加载圆形扫描</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-scan-circle ref="scan" @ready="subReady" :radius="radius" :interval="interval" :color="color" :position="position"></vc-scan-circle>
        <vc-layer-imagery>
          <vc-provider-imagery-tianditu map-style="img_c" token="436ce7e50d27eede2f2929307e6b33c0"></vc-provider-imagery-tianditu>
        </vc-layer-imagery>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
<h4>代码</h4>
<pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
